import { injectHTML } from '@/utils/utils';
import { CheckoutParams, GooglePurchaseParams, IConversion, SearchParams } from './types';

export class GoogleConversion implements IConversion {
  public static initialized = false;

  constructor(private conversionId: string) {
    this.init();
  }

  public init() {
    if (GoogleConversion.initialized) return;

    const script = `
      <script async src="https://www.googletagmanager.com/gtag/js?id=${this.conversionId}"></script>
      <script>
        window.dataLayer = window.dataLayer || [];
        function gtag() { dataLayer.push(arguments); }
        gtag('js', new Date());
        gtag('config', '${this.conversionId}');
      </script>
    `;
    injectHTML(script, 'head', 'first');

    GoogleConversion.initialized = true;
  }

  public trackSearch({ from_date, to_date }: SearchParams) {
    this.trackEvent('search', {
      from_date,
      to_date,
    });
  }
  public trackCheckout({ currency, value }: CheckoutParams): void {
    this.trackEvent('begin_checkout', {
      value,
      currency,
    });
  }

  private static isGtagAvailable(): boolean {
    return typeof window !== 'undefined' && typeof window.gtag === 'function';
  }

  public trackPurchase({ currency, roomnights, transaction_id, value }: GooglePurchaseParams): void {
    this.trackEvent('purchase', {
      value,
      currency,
      roomnights,
      transaction_id,
    });
  }

  /**
   * Sends an event to Google Analytics.
   *
   * @param eventName The name of the event.
   * @param eventParams Optional parameters to send with the event.
   */
  private trackEvent(eventName: string, eventParams?: { [key: string]: any }): void {
    if (GoogleConversion.isGtagAvailable()) {
      window.gtag!('event', eventName, eventParams);
      console.log(`GA4 Event Tracked: ${eventName}`, eventParams || {});
    } else {
      console.warn('Google Analytics (gtag) is not available. Event not tracked:', eventName, eventParams || {});
    }
  }
  public trackConversion() {}
}
